jQuery(document).ready(function ($) {

    var Salvi = (function () {

        var introPartSlider = function () {

            // intros Part Slider
            $introSlider = $(".intro-slider");
            $introSliderNavigation = $(".intro-slider-navigation");

            let slideCount = null;

            $introSlider.on("init", function (event, slick) {
                slideCount = slick.slideCount;
                setSlideCount();
                setCurrentSlideNumber(slick.currentSlide);
            });

            $(".intro-slider").slick({
                arrows: true,
                autoplay: true,
                autoplaySpeed: 3000,
                dots: false,
                infinite: true,
                arrows: false,
                speed: 750,
                // fade: true,
                cssEase: 'ease',
                swipe: true,
                swipeToSlide: true,
                touchThreshold: 10
                // prevArrow: '<i class="fal fa-long-arrow-left prev"></i>',
                // nextArrow: '<i class="fal fa-long-arrow-right next"></i>',
                // appendArrows: '.intro-slider-navigation'
            });

            $introSlider.on("beforeChange", function (event, slick, currentSlide, nextSlide) {
                setCurrentSlideNumber(nextSlide);
            });

            function setSlideCount() {
                let $el = $('.slide-count-wrap').find('.total');
                $el.text(slideCount);
            };

            function setCurrentSlideNumber(currentSlide) {
                let $el = $('.slide-count-wrap').find('.current');
                $el.text(currentSlide + 1);
            };

            $introSliderNavigation.find('.prev').click(function () {
                $introSlider.slick('slickPrev');
            });

            $introSliderNavigation.find('.next').click(function () {
                $introSlider.slick('slickNext');
            });

        };

        var testimonialPartSlider = function () {

            $(".testimonial-slider").slick({
                dots: true,
                infinite: true,
                arrows: false,
                fade: true
                // prevArrow: '<i class="fal fa-long-arrow-left prev"></i>',
                // nextArrow: '<i class="fal fa-long-arrow-right next"></i>',
                // appendArrows: '.intro-slider-navigation'
            });

        };

        var communitySlider = function () {

            $(".community-slider").slick({
                dots: true,
                infinite: true,
                arrows: false,
                fade: true,
                // prevArrow: '<i class="fal fa-long-arrow-left prev"></i>',
                // nextArrow: '<i class="fal fa-long-arrow-right next"></i>',
            });
        };

        var sameHeight = function () {

            $('.community_img').matchHeight();
            $('.team-wrap .sameHeight').matchHeight();
            $('.contact-location-container').matchHeight();
            $('.squares-grid .item.sameHeight').matchHeight();
            $('#single-pa-ten .pa-box.sameHeight').matchHeight();
            $('#case-results-top-cases .col-result .wrap-result.sameHeight').matchHeight();
            $('#case-results-cases .col-md-3 .wrap-result.sameHeight').matchHeight();

            enquire.register("screen and (min-width: 992px) and (max-width: 1199px)", {
                setup: function () {
                    // Load in content via AJAX (just the once)
                },
                match: function () {
                    // Show sidebar
                    $('.testimonials-part .cols').matchHeight();

                },
                unmatch: function () {
                    // Hide sidebar
                }
            });

        };

        var originalJS = function () {


            $('input, textarea').on('focus', function () {
                if ($(this).attr('placeholder')) $(this).data('placeholder', $(this).attr('placeholder')).removeAttr('placeholder');
            }).on('blur', function () {
                if ($(this).data('placeholder')) $(this).attr('placeholder', $(this).data('placeholder')).removeData('placeholder');
            });

            //Skip Link
            var skipLink = function () {
                $(".skiplink").focusin(function () {
                    $(this).css("position", "static");
                });

                $(".skiplink").focusout(function () {
                    $(this).css("position", "absolute");
                });
            }

            //No comments
            $("#commentform").addClass("noauto");

            //Full Screen Menu
            $(".btn-menu").on("click", function () {
                $(".full-screen-menu").addClass("nav-active");
                $('.full-screen-menu-close').delay(400).queue(function () {
                    $(this).addClass("close-active").dequeue();
                });
            });

            $(".full-screen-menu-close").on("click", function () {
                $(this).removeClass("close-active");
                $(".full-screen-menu").delay(250).queue(function () {
                    $(this).removeClass("nav-active").dequeue();
                });
            });

            /* Menu */
            $(".dropdown a").click(function () {
                $(this).siblings('.dropdown-menu').slideToggle();
            });

            //Hover for Desktop, click for mobile nav
            /*if ($(window).width() > 767) {
                $('.dropdown').on('mouseenter mouseleave click tap', function() {
                    $(this).toggleClass("open");
                });
            }*/

            //Stop Video once modal closes

            $(".page-template-practice-area .modal, .single-attorney .modal, .page-template-press .modal").on('hidden.bs.modal', function (e) {
                var $this = $(this);
                var $frame = $this.find('.modal-content #video_play iframe');
                $frame.attr("src", $frame.attr("src").replace("autoplay=1", "autoplay="))
                //$frame.attr("src", $frame.attr("src"));
            });

        };


        var twoColumns = function () {
            // split faq into two columns
            var cols = 2,
                parent = $('#single-pa-seven .qa-category'),
                listItem = $('.qa-faq'),
                newListClass = 'faq-col';

            parent.each(function () {
                var faqs_per_col = new Array(),
                    faqs = $(this).find(listItem),
                    min_faqs_per_col = Math.floor(faqs.length / cols),
                    difference = faqs.length - (min_faqs_per_col * cols);
                for (var i = 0; i < cols; i++) {
                    if (i < difference) {
                        faqs_per_col[i] = min_faqs_per_col + 1;
                    } else {
                        faqs_per_col[i] = min_faqs_per_col;
                    }
                }
                for (var i = 0; i < cols; i++) {
                    $(this).append($('<div ></div>').addClass(newListClass));
                    for (var j = 0; j < faqs_per_col[i]; j++) {
                        var pointer = 0;
                        for (var k = 0; k < i; k++) {
                            pointer += faqs_per_col[k];
                        }
                        $(this).find('.' + newListClass).last().append(faqs[j + pointer]);
                    }
                }
            });
        };

        /* TABLE OF CONTENTS */
        var toc = function () {

            var ToC =
                "<div class='contents-table-wrap'><span class='btn-contents-table'><i class='fas fa-chevron-up'></i>Table of Contents</i></span>" +
                "<div class='contents-table'>" +
                "<div class='info-details'>" +
                "<ul class='news-list'>" +
                "<li class='first'><a href='#'>Overview</a></li>";

            var newLine, el, title, link;

            $(".toc-heading").each(function () {

                el = $(this);
                title = el.text();
                link = "#" + el.attr("id");

                newLine =
                    "<li>" +
                    "<a class='toc-link' href='" + link + "'>" +
                    title +
                    "</a>" +
                    "</li>";

                ToC += newLine;

            });

            ToC += "</ul></div></div></div>";

            $(".toc-wrap").prepend(ToC);

            enquire.register("screen and (min-width: 992px)", {
                match: function () {

                    var $toggle = 0;
                    $('.btn-contents-table').on('click', function () {
                        $(this).parent().toggleClass('opened');
                    });

                    if ($('.contents-table-wrap').length) {

                        var sticky = new Waypoint.Sticky({
                            element: $('.contents-table-wrap')
                        });

                    }

                },
                unmatch: function () {

                }
            });

            $('.contents-table').smoothScroll();

        };

        var videoHomepageModal = function () {

            if ($('.hero-home').length > 0) {

                $(".video-modal").each(function (index) {
                    $(this)[0].pause();
                });

                $('.video-modal').removeAttr("autoplay");

                $('.open-popup-link').magnificPopup({
                    type: 'inline',
                    mainClass: 'mfp-fade',
                    preloader: true,
                    callbacks: {
                        open: function () {
                            // Will fire when this exact popup is opened
                            // this - is Magnific Popup object

                            $(".video-modal").each(function (index) {
                                $(this)[0].play();
                            });

                            $('#video-background')[0].pause();

                            // $('.video-modal')[0].addEventListener('ended', function(e) {
                            //     $('.video-modal')[0].pause();
                            //     $('.video-modal')[0].currentTime = 0;
                            // });

                        },
                        close: function () {
                            // Will fire when modal is closed

                            $('#video-background')[0].play();

                            $(".video-modal").each(function (index) {
                                $(this)[0].pause();
                                $(this)[0].currentTime = 0;
                            });

                        }
                    },
                    midClick: true // Allow opening modal on middle mouse click. Always set it to true if you don't provide alternative source in href.
                });

            }

        };

        var attorneyVideo = function () {
            // Change modal video
            if ($('.attorney-video-item').length > 0) {
                $('.attorney-video-item a').click(function (e) {
                    e.preventDefault();
                    $.ajax({
                        type: 'POST',
                        url: cws.ajaxurl,
                        data: {
                            action: 'cws_video_ajax_callback',
                            video_select: $(this).attr('data-shortcode'),
                        },
                        dataType: 'json',

                        success: function (response) {

                            if (response.success === true) {
                                $('#paVid3 .modal-content .modal-body #video_play').html(response.data.shortCode);
                                var frame = $('#paVid3 .modal-content .modal-body #video_play iframe');
                                frame.attr("src", frame.attr("data-src"));
                                $('#paVid3').modal('show');
                            }
                        },
                    });
                });

                //Stop Video once modal closes
                $("#paVid3").on('hidden.bs.modal', function (e) {
                    $("#paVid3 iframe").attr("src", $("#paVid3 iframe").attr("src"));
                });

                // Play video on play
                $("#paVid3").on('shown.bs.modal', function (ev) {
                    var $this2 = $(this);
                    var $frame2 = $this2.find('.modal-content .modal-body #video_play iframe');
                    $frame2.attr("src", $frame2.attr("data-src"));
                });
            }
        }

        var loadMore = function () {
            $('.load-more a').on('click', function () {

                var btn = $(this);
                console.log(btn);

                var contentTarget = btn.parent().data('target');
                console.log(contentTarget);

                var appendTo = btn.parent().data('append-to');
                console.log(appendTo);

                var loadMore = btn.parent().data('load-more');
                console.log(loadMore);

                $.get(btn.attr('href'), function (data) {

                    console.log('okok');

                    $(appendTo).append($(data).find(contentTarget));

                    var nextBtn = $(data).find(loadMore).attr('href');

                    if (nextBtn) {
                        btn.attr('href', nextBtn);
                    } else {
                        btn.parent().hide();
                    }

                });

                return false;

            });
        };

        var faqs = function () {

            $('.qa-faqs h3 a').on('click', function (event) {
                event.preventDefault();
                $(this).stop().toggleClass('active');
                $(this).parent().toggleClass('open');

            });

        };

        return {

            init: function () {
                introPartSlider();
                testimonialPartSlider();
                communitySlider();
                sameHeight();
                originalJS();
                twoColumns();
                videoHomepageModal();
                // attorneyVideo();
                loadMore();
                toc();
                faqs();
            }

        }

    })();

    Salvi.init();

});


